import { offersApi } from '../../utils/api';

const OffersActions = {
    setItems: (items) => ({
        type: 'OFFERS:SET_ITEMS',
        payload: items
    }),
    fetchItems: () => dispatch => {
        offersApi.get().then(( { data } ) => {
            dispatch(OffersActions.setItems(data))
        });
    }
};

export default OffersActions;