import React from 'react';

import { Link } from 'react-router-dom';

class Control extends React.Component {
    render() {
        let nums = new Array();

        for (let i = 0; i < this.props.count; i++) {
            nums.push(i);
        }

        console.log(this.props.count);

        return (
            <div className="control">
                <div className="arrow arrow-left" onClick={(e, number = this.props.current != 0 ? this.props.current - 1 : this.props.count - 1) => this.props.handle(number)}></div>
                <ul>
                    { nums.map(num => {
                        console.log("c: " + this.props.current);
                        if (num == this.props.current) {
                            return <li className="active" onClick={(e, number = num) => this.props.handle(number)} key={num}>{num < 9 ? '0' + (num + 1) : num + 1}</li>
                        } else {
                            return <li onClick={(e, number = num) => this.props.handle(number)} key={num}>{num < 9 ? '0' + (num + 1) : num + 1}</li>
                        }
                    }) }
                </ul>
                <div className="arrow arrow-right" onClick={(e, number = this.props.current != this.props.count - 1 ? this.props.current + 1 : 0) => this.props.handle(number)}></div>
            </div>
        );
    }
}

export default Control;