import React from 'react';
import { withRouter } from 'react-router';
import { buyAppsApi } from '../utils/api/';

import axios from 'axios';

import Input from './Input';

class BuyForm extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            name: null,
            number: null
        }
    } 

    submitHandle = (e) => {
        e.preventDefault();

        let nameInput = document.getElementById('buyFormName');
        let numberInput = document.getElementById('buyFormNumber');
        let form = document.getElementById('buyForm');
        
        if (nameInput.value && numberInput.value) {
            const app = {
                offerId: form.hasAttribute('offerId') ? form.getAttribute('offerId') : null,
                name: nameInput.value,
                number: numberInput.value
            }

            axios.post(`http://localhost:3333/buy-app/`, { 
                offerId: form.hasAttribute('offerId') ? form.getAttribute('offerId') : null,
                name: nameInput.value,
                number: numberInput.value
             })
                .then(res => {
                    nameInput.value = "";
                    numberInput.value = "";
                    form.style.display = "none";
                })
            }
        }

    render() {
        return (
            <div style={this.props.style} className="buy-form-content">
                <h3>Введіть ваші дані</h3>
                <Input class="buy-form-name-input" type="standart" id="buyFormName" placeholder="Ваше ім'я" />
                <Input class="buy-form-number-input" type="standart" id="buyFormNumber" placeholder="Номер телефону" />
                <button id="buyFromSubmit" onClick={(e, match) => this.submitHandle(e, match)}>НАДІСЛАТИ</button>
            </div>
        );
    }
}

export default BuyForm;