import React from 'react';

import imgPath from '../../../imgPath';

class MiniCard extends React.Component {
    render() {
        const bg = {
            background: `url("${imgPath + this.props.mark + '-' + this.props.model + '-' + this.props.year + '-' + this.props.colors[1] + '.jpg'}") no-repeat center`,
            backgroundSize: '130px auto'
        }

        return (
            <div className="mini-card">
                <span className="mc-price">від 71 000$</span>
                <div className="mc-img" style={bg}></div>
                <span className="mc-name">{this.props.mark ? this.props.mark.toUpperCase() + " " + this.props.model.toUpperCase() + " " + this.props.year : ''}</span>
            </div>
        );
    }
}

export default MiniCard;