import React from 'react';

import Button from '../Button';

import { Link } from 'react-router-dom'; 

class Price extends React.Component {
    render() {
        return (
            <div className="car-price">
                <ul>
                    <li>
                        <h3>Ціна авто:</h3>
                        <span className="price-text">від 71 000$</span>
                    </li>
                    <li>
                        <h3>В кредит:</h3>
                        <span className="price-text">від 1010$ <span className="price-text-small">/міс.</span></span>
                    </li>
                    <Link to={'/details/' + this.props._id}><Button color="dark" text="ДЕТАЛЬНІШЕ" /></Link>
                </ul>
            </div>
        );
    }
}

export default Price;