import React, { useContext } from 'react';
import logo from './logo.svg';
import './App.css';

import Header from './components/header/Header';
import BuyForm from './components/BuyForm';

import { useTransition, animated } from 'react-spring';

// import Main from './components/Main';
// import Catalog from './components/Catalog';
// import Details from './components/Details';

import { Main, CarsCatalog, Details } from './modules';  

import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  __RouterContext,
  withRouter
} from "react-router-dom";

const App = () => {
    let bgColor = new Object();

    const { location } = useContext(__RouterContext);
    const transitions = useTransition(location, location => location.pathname, {
      from: { opacity: 0, transform: "translate(0, 100%)"},
      enter: { opacity: 1, transform: "translate(0, 0%)" },
      leave: { opacity: 0, transform: "translate(0, 30%)" },
      config: { duration: 200 }
    });

    return  (
         <div id="App" className="App">
         <div id="buyForm" className="buy-form">
            <div className="buy-form-close-button" onClick={() => {
                document.getElementById("buyForm").style.display = "none";
              }}><img src="/img/close.svg" alt="close icon"/></div>
              <BuyForm />
         </div>
         <Header />
          {transitions.map(( { item, props, key} ) => (
            <animated.div key={key} style={props} className="h100">
              <Switch location={item}>
                <Route path="/" exact component={Main} />
                <Route path="/catalog" exact component={CarsCatalog} style={{transition: '.5s'}}/>
                <Route path="/details/:id" exact component={Details} />
              </Switch>
            </animated.div>
          ))}
        {/* <Catalog /> */}
        {/* <Details /> */}
      </div>
    );
}

export default App;
