import React from 'react';

import Color from '../offer/car/color-picker/Color';

class OfferCard extends React.Component {
    render() {
        const listColors = this.props.colors.map((color) => 
            <li><Color color={color} border="1px solid silver" /></li>
        );
        return (
            <div className="offer-card">
                <div className="oc-colors">
                    <ul>
                        {listColors}
                    </ul>
                </div>
                <span className="oc-add-options">{this.props.options.length} додаткових опцій <img src="/img/arrow-down.svg" alt="arrow down"/></span>
                <div className="oc-price-location">
                    <div className="oc-pl-wrapper">
                        <span className="oc-price">{this.props.price} $</span>
                        <span className="oc-location"><img src="/img/map-pin.svg" alt="map pin icon"/>{this.props.location}</span>
                    </div>
                </div>

                <div style={ this.props.id == 'offer0' ? { background: "#37a64f", border: "1px solid #37a64f" } : {}} onClick={(e, id) => {this.props.handle(e, this.props.id)}} className="oc-check" id={this.props.id + 'Check'}>
                    <img style={ this.props.id == 'offer0' ? { display: 'block' } : {}} id={this.props.id + 'Tick'} className="oc-check-tick" src="/img/tick-light.svg" alt="tick icon"/>
                </div>
            </div>
        );
    }
}

export default OfferCard;