import React from 'react';

import CarImage from './CarImage';
import ColorPicker from './color-picker/ColorPicker';

import imgPath from '../../../imgPath';

class Car extends React.Component {
    render() {
        console.log(this.props);

        return (
            <div className="car">
                <CarImage img={this.props.img && this.props.mark ? this.props.img.substr(-3) != "jpg" ? this.props.img + '-' + this.props.colors[0] + '.jpg' : this.props.img : ''} />
                <span className="car-name">{this.props.mark.toUpperCase() + " " + this.props.model.toUpperCase() + " " + this.props.year}</span>
                <ColorPicker imgPath={imgPath + (this.props ? (this.props.mark.toLowerCase() + '-' + this.props.model.toLowerCase() + "-" + this.props.year) : '')} type="main" colors={this.props.colors} handle={this.props.handle} />
            </div>
        );
    }
}

export default Car;