import React from 'react';

import OfferCard from './OfferCard';
import Button from '../../components/Button';

import BuyForm from '../../components/BuyForm';

class Offers extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            checked: 'offer0'
        }
    }

    checkHandle = (e = null, id) => {
        const checkOld = document.getElementById(this.state.checked + "Check");
        const tickOld = document.getElementById(this.state.checked + "Tick");
        const checkNew = document.getElementById(id + "Check");
        const tickNew = document.getElementById(id + "Tick");

        if (checkOld && tickOld) {
            checkOld.style.background = "#fff";
            checkOld.style.border = "1px solid silver";
            tickOld.style.display = "none";
        }

        if (checkNew && tickNew) {
            checkNew.style.background = "#37a64f";
            checkNew.style.border = "1px solid #37a64f";
            tickNew.style.display = "block";    
        }
       
        this.setState({
            checked: id
        });
    }

    buyHandle = () => {
        let form = document.getElementById('buyForm');
        form.style.display = 'flex';

        form.setAttribute('offerId', this.props.items[parseInt(this.state.checked.substr(-1))] ? this.props.items[parseInt(this.state.checked.substr(-1))]._id : null);
    }

    componentDidMount() {
        this.checkHandle(null, 'offer0');
    }

    render() {    
        let buy = (
            <div>
                <div className="offers-price">
                    <div className="over-price">
                        <h3>Ціна:</h3>
                        <span>{this.props.items[parseInt(this.state.checked.substr(-1))] ? this.props.items[parseInt(this.state.checked.substr(-1))].price + ' $' : ''}</span>
                    </div>
                    <div className="in-credit">
                        <h3>В кредит:</h3>
                        <span>{this.props.items[parseInt(this.state.checked.substr(-1))] ? this.props.items[parseInt(this.state.checked.substr(-1))].creditPrice + ' $ ' : ''}<span className="op-small">\міс.</span></span>
                    </div>
                </div>

                <div className="offers-buttons">
                    <Button handle={this.buyHandle} style={{ background: "#000", color: "#fff" }} className="offers-buy-but" text="КУПИТИ" />
                    <Button style={{ background: 'transparent', color: '#000', border: '1px solid #000' }} className="offers-trade-but" text="ТРЕЙД-ІН" fill="none" />
                </div>
            </div>
        );

        
        return (
            <div className="offers">
                <h1>Пропозиції</h1>

                <div className="offers-cards">
                {
                    this.props.items ? this.props.items.map((offer, i) => (
                        <OfferCard colors={this.props.items.colors} id={'offer' + i} handle={this.checkHandle} {...offer} />
                    )) : "Loading..."
                } 
                </div>

                {
                    this.props.items ? buy : ''
                }
             
            </div>
        );
    }
}

export default Offers;