import React from 'react';

import Card from './Card';

import { Link } from 'react-router-dom';

import imgPath from '../../imgPath';

class Cars extends React.Component {
    render() {
        return (
            <div className="cars">
                { this.props.items ? this.props.items.map(car => (
                    <Link to={'/details/' + car._id}>
                        <Card bg={imgPath + car.mark + '-' + car.model + '-' + car.year + '-' + car.colors[1] + '.jpg'} {...car} />
                    </Link>
                )) : 'Loading...' }
            </div>
        );
    }
}

export default Cars;