import React from 'react';

import { connect } from 'react-redux';
import Offers from '../../../components/offers/Offers';
import { withRouter }   from 'react-router';
import OffersActions from '../actions';

class OffersContainer extends React.Component {

    componentWillMount() {
        const { fetchItems } = this.props;
        fetchItems();
    }

    render() {
        console.log("hello " + this.props.items);
        return <Offers items={this.props.items} />
    }

}

const matchParams = ({ offers }, { match }) => ({
    offers: offers.items && offers.items.filter(offer => offer.car == match.params.id),
});

const filt = (arr, id) => {
    let newArr = new Array();

    if (arr.items) {
        arr.items.forEach(offer => {
            if (offer.carId == id) {
                newArr.push(offer);
            }
        }); 
    } 

    console.log(newArr);
    return {
        items: newArr
    };
}

export default withRouter (connect(({ offers }, { match }) => {return filt(offers, match.params.id)}, OffersActions)(OffersContainer));