import React from 'react';
import './css/Main.css';

import Header from './header/Header';
import Offer from './offer/Offer';
import Footer from './footer/Footer';

class Main extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            currentCar: 0,
            itemsCount: this.props.items ? this.props.items.length : 0
        };
    }    
    
    carHandle = (newIndex) => {
        this.setState({
            currentCar: newIndex
        });

        const styles = [
            {   
                background: "linear-gradient(312deg, rgba(255,255,255,1) 0%, rgba(226,226,226,1) 100%)",
                headers: "#444444",
                text: "#000000",
                buttons: "#000000",
                buttonsText: "#ffffff",
                color1: "#8B8B8B",
                logo: "dark"
            },
            {   
                background: "linear-gradient(159deg, rgba(45,45,45,1) 0%, rgba(17,17,17,1) 100%)",
                headers: "#ffffff",
                text: "#C5C5C5",
                buttons: "#ffffff",
                buttonsText: "#000000",
                color1: "#8B8B8B",
                logo: "light"
            },
            {   
                background: "linear-gradient(317deg, rgba(61,1,1,1) 0%, rgba(161,49,49,1) 100%)",
                headers: "#ffffff",
                text: "#f5f5f5",
                buttons: "#ffffff",
                buttonsText: "#000000",
                color1: "#8B8B8B",
                logo: "light"
            },
            {   
                background: "linear-gradient(317deg, rgba(5,6,28,1) 0%, rgba(37,38,59,1) 100%)",
                headers: "#ffffff",
                text: "#f5f5f5",
                buttons: "#ffffff",
                buttonsText: "#000000",
                color1: "#8B8B8B",
                logo: "light"
            },
            {   
                background: "linear-gradient(317deg, rgba(42,42,42,1) 0%, rgba(66,66,66,1) 100%)",
                headers: "#ffffff",
                text: "#f5f5f5",
                buttons: "#ffffff",
                buttonsText: "#000000",
                color1: "#8B8B8B",
                logo: "light"
            },
            {   
                background: "linear-gradient(319deg, rgba(23,24,25,1) 0%, rgba(23,24,28,1) 100%)",
                headers: "#ffffff",
                text: "#f5f5f5",
                buttons: "#ffffff",
                buttonsText: "#000000",
                color1: "#8B8B8B",
                logo: "light"
            }
        ];

            // let menu = document.querySelectorAll('.menu-li');
            // menu.style.transition = '0.5s';
            // menu.style.color = styles[e.target.id].text;

            const allColors = ["ffffff", "000000", "941c1c", "1a1d2d", "5e8887", "21282e"];

            const getColorIndex = (color) => {
                let toReturn = 0;

                allColors.forEach((el, i) => {
                    if (el == color.toLowerCase()) {
                        toReturn = i;
                    }
                });

                return toReturn;
            }

            let i = getColorIndex(this.props.items[this.state.currentCar].colors[0]);     
            // console.log(color);           

                let logo = document.getElementById('logo');
                logo.src = "/img/logo-" + styles[i].logo + ".svg";

                let mesIcon = document.getElementById('messageIcon');
                mesIcon.src = "/img/message-icon-" + styles[i].logo + ".svg";

                let glassIcon = document.getElementById('glassIcon');
                glassIcon.src = "/img/glass-icon-" + styles[i].logo + ".svg";

                document.body.style.setProperty('--bg', styles[i].background);
                document.body.style.setProperty('--headers', styles[i].headers);
                document.body.style.setProperty('--text', styles[i].text);
                document.body.style.setProperty('--buttons', styles[i].buttons);
                document.body.style.setProperty('--buttonsText', styles[i].buttonsText);
                document.body.style.setProperty('--color-1', styles[i].color1);
    }

    render() {
        if (this.props.items) {
            console.log("cur car : " + this.state.currentCar);

            return (
                <div className="main">
                           { this.props.items.map((car, i) => {
    
                            if (i == this.state.currentCar) {
                                return (
                                    <Offer first="true" key={i} item={car} />
                                );
                            } else {
                                return (
                                    <Offer key={i} item={car} hide="true" />
                                );
                            }
                            }) 
                           }
                        
                        <Footer current={this.state.currentCar} type="control" handle={this.carHandle} itemsCount={this.props.items.length} /> 
                </div>
            );
        } else {
            return (
                <div>

                </div>
            );
        }
    }   
}

export default Main;
