import { carsApi } from '../../utils/api';

const MainActions = {
    setItems: (items) => ({
        type: 'CARS:SET_ITEMS',
        payload: items
    }),
    fetchItems: () => dispatch => {
        carsApi.get().then(( { data } ) => {
            dispatch(MainActions.setItems(data))
        }); 
    }
};

export default MainActions;
