import React from 'react';

import { connect } from 'react-redux';
import Catalog from '../../../components/Catalog';

import CarsListActions from '../actions';

class CatalogContainer extends React.Component {

    componentWillMount() {
        const { fetchItems } = this.props;
        fetchItems();
    }
    
    render() {
        return <Catalog items={this.props.items} />;
    }

}

export default connect(({ cars }) => cars, CarsListActions,)(CatalogContainer);