import React from 'react';

import { Link } from 'react-router-dom';

class Menu extends React.Component {
    componentDidMount() {
        let list = document.querySelectorAll('.menu-li');
        list.forEach(li => {
            li.firstChild.classList.add("menu-li-a");
        });
    }

    render() {
        return (
            <div className="menu">
                <ul>
                    <li className="menu-li">
                        <Link to="/" className="menu-li-a">ГОЛОВНА</Link>
                    </li>
                    <li className="menu-li">
                        <Link to="/catalog">КАТАЛОГ</Link>
                    </li>
                    <li className="menu-li"><Link to="#">ПРО НАС</Link></li>
                    <li className="menu-li"><Link to="#">ВІДГУКИ</Link></li>
                    <li className="menu-li"><Link to="#">КОНТАКТИ</Link></li>
                </ul>
            </div>
        )
    }
}

export default Menu;