const BOYER = (S, P) => {
    var ret = [];

    var k = P.length - 1, o = {}, l = S.length, i = 0, j, c;
    for (; i < k; i++)
        o[P.charAt(i)] = k - i;
    i = 0;
    while (i < l) {
        for (j = k; j >= 0; j--)
            if (P.charAt(j) != S.charAt(i + j)) {
                break;
            }
        if (j < 0) {
            i++;
            ret.push(i);
        }
        else {
            c = o[S.charAt(i + j)];
            if (!c)
                c = k + 1;
            c += j - k;
            if (c <= 0) 
                c = 1;
            i += c;
        }
    }
    return ret;
}

export default BOYER;