import React from 'react';

class Properties extends React.Component {
    render() {
        let acc = this.props.params.acceleration;

        return (
            <div className="car-props">
                <ul>
                    <li>
                        <h3>Розхід</h3>
                        <span>{this.props.params.fuelCons}</span>
                    </li>
                    <li>
                        <h3>Двигун</h3>
                        <span>{this.props.params.engine}</span>
                    </li>
                    <li>
                        <h3>0-100 км/год</h3>
                        <span>{
                            (acc / 100) + " сек."
                        }</span>
                    </li>
                    <li>
                        <h3>Привід</h3>
                        <span>{this.props.params.wheelDrive}</span>
                    </li>
                </ul>
            </div>
        );
    }
}

export default Properties;