import React from 'react';

import Course from './Course';
import Control from './Control';
import Question from './Question';

class Footer extends React.Component {
    render() {
        if (this.props.type == "control") {
            return (
                <footer className="footer">
                    <Course />
                    <Control current={this.props.current} handle={this.props.handle} count={this.props.itemsCount} />
                    <Question />
                </footer>
            );
        } else if (this.props.type == "search-results") {
            return (
                <footer className="footer">
                    <Course />
                    <span className="search-results">Знайдено {this.props.itemsCount} авто</span>
                    <Question />
                </footer>
            );
        }
        
    }
}

export default Footer;