import React from 'react';
import './css/Catalog.css';

import Header from './header/Header';
import Filters from './filters/Filters';
import Cars from './cars/Cars.jsx';
import Footer from './footer/Footer';
 
import BOYER from '../algs/boyer-moor';

class Catalog extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            items: null,
            drive: null,
            engine: null,
            searchByName: null,
            itemsCount: this.props.items ? this.props.items.length : 0
        }

        const style = {   
            background: "linear-gradient(312deg, rgba(255,255,255,1) 0%, rgba(226,226,226,1) 100%)",
            headers: "#444444",
            text: "#000000",
            buttons: "#000000",
            buttonsText: "#ffffff",
            color1: "#8B8B8B",
            logo: "dark"
        };
        
            let logo = document.getElementById('logo');
        
        if (logo) {
            logo.src = "/img/logo-" + style.logo + ".svg";
            
            let mesIcon = document.getElementById('messageIcon');
            if (mesIcon) {
                mesIcon.src = "/img/message-icon-" + style.logo + ".svg";
            }

            let glassIcon = document.getElementById('glassIcon');
            glassIcon.src = "/img/glass-icon-" + style.logo + ".svg";

            document.body.style.setProperty('--bg', style.background);
            document.body.style.setProperty('--headers', style.headers);
            document.body.style.setProperty('--text', style.text);
            document.body.style.setProperty('--buttons', style.buttons);
            document.body.style.setProperty('--buttonsText', style.buttonsText);
            document.body.style.setProperty('--color-1', style.color1);
        }
    }

    filter = (filters) => {
        let list1 = new Array();
        if (filters.drive) {
            this.props.items.forEach(car => {
                if (car.params.wheelDrive == filters.drive) {
                    list1.push(car);
                } 
            });
        } else {
            list1 = this.props.items;
        }

        let list2 = new Array();
        if (filters.engine) {   
            list1.forEach(car => {
                if (car.params.engine.split(',')[0] == filters.engine) {
                    list2.push(car);
                } 
            });
        } else {
            list2 = list1;
        }

        let list3 = new Array();
        if (filters.searchByName) {
            list2.forEach(car => {
                if (BOYER(car.mark.toLowerCase() + " " + car.model.toLowerCase() + " " + car.year.toString(), filters.searchByName.toLowerCase()).length > 0) {
                    list3.push(car);
                }
            });
        } else {
            list3 = list2;
        }

        this.setState({
            items: list3,
            itemsCount: list3 ? list3.length : null
        });
    }

    filtersHandle = (drive, engine, searchByName) => {
        let newFilters = new Object();

        if (drive != 'Всі') {
            newFilters["drive"] = drive;
        } else {
            newFilters["drive"] = null;
        }

        if (engine != 'Всі') {
            newFilters["engine"] = engine;
        } else {
            newFilters["engine"] = null;     
        }

        if (searchByName) {
            newFilters["searchByName"] = searchByName;
        }
        
        this.filter(newFilters);       
    }

    searchByName = (e, id) => {
        console.log(id);
        let input = document.getElementById(id);
        let newItemsList = new Array();

        if (input.value) {
            this.props.items.forEach(car => {
                if (BOYER(car.mark.toLowerCase() + " " + car.model.toLowerCase() + " " + car.year.toString(), input.value.toLowerCase()).length > 0) {
                    newItemsList.push(car);
                }
            });    
        } else {
            newItemsList = this.props.items;
        }
            

        this.setState({
            items: newItemsList
        });
    }

    render() {
        if (!this.state.items) {
            if (this.props.items) {
                this.setState({
                    items: this.props.items,
                    itemsCount: this.props.items.length
                });
            }
        }
        
        return (
            <div className="catalog">
                {/* <Header /> */}
                <div className="catalog-content">
                    <Filters searchByName={this.searchByName} handle={this.filtersHandle} />
                    <Cars items={this.state.items} />    
                </div>
                <Footer itemsCount={this.state.itemsCount} type="search-results" />
            </div>
        );
    }
}

export default Catalog;