import React from 'react';

class Button extends React.Component {
    render() {
        let style = new Object();

        if (this.props.fill == "none") {
            style["border"] = "1px solid #000";
            style["color"] = "#000";
        } else if (this.props.color == 'dark') {
            // style["background"] = "#000";
            // style["color"] = "#fff";
        }

        return (
            <button onClick={this.props.handle ? (e, id, text) => {this.props.handle()} : () => {}} className='button' style={this.props.style}>
                {this.props.text}
            </button>
        )
    }
}

export default Button;