import React from 'react';

class CarImage extends React.Component {
    render() {
        const bg = {
            background: 'url("' + this.props.img + '") center center / 550px auto no-repeat',
        }

        if (this.props.align == "bottom") {
            bg["backgroundPosition"] = "center top";
        }

        return (
            <div className="car-img" style={bg}>
                
            </div>
        );
    }
}

export default CarImage;