import { carsApi } from '../../utils/api';

const CarsListActions = {
    setItems: (items) => ({
        type: 'CARS:SET_ITEMS',
        payload: items
    }),
    appendItem: item => ({
        type: 'CARS:APPEND_ITEM',
        payload: item,
    }),
    fetchItems: (id) => dispatch => {
        carsApi.getById(id).then(( { data } ) => {
            dispatch(CarsListActions.appendItems(data))
        });
    },  
    fetchItems: () => dispatch => {
        carsApi.get().then(( { data } ) => {
            dispatch(CarsListActions.setItems(data))
        });
    }  
};


export default CarsListActions;