import React from 'react';

class Checkboxes extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            checked: this.props.checked
        };
    }

    componentDidMount() {
        if (this.props.text == 'Всі') {
            this.props.handle(this.props.id, this.props.text);
        }
    }

    render() {
        return (
            <div className="checkbox">
                <img id={this.props.id + "Tick"} src="/img/tick.svg" alt="tick icon"/>        
                <label onClick={(e, id, value) => { this.props.handle(this.props.id, this.props.text) }} className="chbox" htmlFor={this.props.id}></label>
                <input id={this.props.id} type="checkbox"/>
                <label className="text-label" htmlFor={this.props.id}>{this.props.text}</label>
            </div>
        );
    }
}

export default Checkboxes;