import React from 'react';

import Properties from './Properties';
import Car from './car/Car';
import Price from './Price';

import imgPath from '../../imgPath';

class Offer extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            image: null,
            first: true
        };
    }

    changeImg = (path) => {
        console.log("path" + path);
        this.setState({
            image: path,
            first: false
        })
    }

    render() {
        const styles = new Object();

        if (this.props.hide === "true") {
            styles["display"] = "none";
        } else {
            styles["display"] = "grid";
        }

        if (this.state.image == null) {
            this.setState({
                image: imgPath + (this.props.item.mark.toLowerCase() + '-' + this.props.item.model.toLowerCase() + "-" + this.props.item.year)
            });
        }

        if (this.props.first && this.state.first) {
            this.changeImg(this.state.image + '-' + this.props.item.colors[0] + '.jpg');
        }

        return (
            <div className="offer" style={styles}>
                <Properties {...this.props.item} />
                <Car first={this.props.first} {...this.props.item} handle={this.changeImg} img={this.state.image} />
                <Price {...this.props.item} />
            </div>
        )
    }
}

export default Offer;