import React from 'react';

import Color from '../offer/car/color-picker/Color';

class Card extends React.Component {
    render() {
        const bg = {
            background: `url("${this.props.bg}") center bottom / 160px no-repeat`,
        }

        const listColors = this.props.colors.map((color) => 
            <li><Color color={color} /></li>
        );

        return (
            <div className="card">
                <span className="car-props">{this.props.params.engine}</span>
                <div className="car-img" style={bg}></div>
                <div className="colors">
                    <ul>
                        {listColors}
                    </ul>
                </div>
                <span className="car-name">{this.props.mark.toUpperCase() + " " + this.props.model.toUpperCase() + " " + this.props.year}</span>
                <span className="car-price">від 71 000$</span>
            </div>
        );
    }
}

export default Card;