import React from 'react';

class Color extends React.Component {
    render() {
        return (
            <div onClick={(e) => this.props.handle(e, this.props.color)} id={this.props.id} className='color' style={{background: '#' + this.props.color, border: this.props.border}}>
            </div>
        );
    }
}

export default Color;