import React from 'react';

import { connect } from 'react-redux';
import Similar from '../../../components/similar/Similar';
import { withRouter }   from 'react-router';

import CarsListActions from '../../CarsCatalog/actions';

class SimilarContainer extends React.Component {

    componentWillMount() {
        const { fetchItems } = this.props;
        fetchItems();
    }
    
    render() {
        return <Similar items={this.props.items} />;
    }
}

const filt = (arr, id) => {
    let newArr = new Array();
    let i = 0;
    let carMark;
    if (arr.items) {
        arr.items.forEach(car => {
            if (car._id == id) {
                carMark = car.mark;
            }
        });

        arr.items.forEach(car => {
            if (car.mark == carMark && i < 3 && car._id != id) {
                newArr.push(car);
                i++;
            }
        });
    }

    return {
        items: newArr
    }
}

export default withRouter( connect(({ cars }, { match }) => filt(cars, match.params.id), CarsListActions,)(SimilarContainer) );