const initialState = {
    items: null,
}

export default (state = initialState, action) => {
    const { type, payload } = action;
    switch(type) {
        case 'CARS:SET_ITEMS': 
            return {
                ...state,
                items: payload,
            };
        case 'CARS:APPEND_ITEM': 
            return {
                ...state,
                items: state.items ? [...state.items, payload] : [payload],
            };
        default: 
            return state;
    }
};