import React from 'react';

import CarImage from '../../components/offer/car/CarImage';
import ColorPicker from '../../components/offer/car/color-picker/ColorPicker';

import Properties from './Properties';
import BackButton from './BackButton';

import imgPath from '../../imgPath';

class CurrentCar extends React.Component {
    constructor(props) {
        super(props);
        
        this.state = {
            image: null
        }
    }

    changeImg = (path) => {
        console.log(path);
        this.setState({
            image: path
        });
    }

    render() {
        const name = this.props.mark ? (this.props.mark.toUpperCase() + " " + this.props.model.toUpperCase() + " " + this.props.year) : '';
        let fontSize = new Object();

        if (name.length > 22) {
            fontSize['fontSize'] = "32px";
        }

        if (this.props.mark) {
            if (this.state.image == null) {
                this.setState({
                    image: imgPath + (this.props.mark.toLowerCase() + '-' + this.props.model.toLowerCase() + "-" + this.props.year)
                });
            }
            return (
                <div className="current-car">
                    <BackButton section="КАТАЛОГ" />
                    <CarImage align="bottom" img={this.state.image ? this.state.image.substr(-3) != "jpg" ? this.state.image + '-' + this.props.colors[0] + '.jpg' : this.state.image : ''} />
                    <span className="cc-car-name" style={fontSize}>{ name }</span>
                    <ColorPicker handle={this.changeImg} imgPath={imgPath + (this.props ? (this.props.mark.toLowerCase() + '-' + this.props.model.toLowerCase() + "-" + this.props.year) : '')} type="details" colors={this.props.colors} />
                    <Properties {...this.props} />
                </div>
            );
        } else {
            return (
                <div></div>
            )
        }
    }
}

export default CurrentCar;