import React from 'react';

class Property extends React.Component {
    render() {
        return (
            <div className="cc-prop">
                <div className="cc-prop-content">
                    <h3>{this.props.header}</h3>
                    <span>{this.props.text}</span>
                </div>
            </div>
        );
    }
}

export default Property;