import React from 'react';

import Input from '../Input';
import Checkbox from '../Checkbox';

class Filters extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            driveFilter: 'driveAll',
            engineFilter: 'engineAll',
            driveText: 'Всі',
            engineText: 'Всі',
            searchByName: null
        }
    }

    driveHandle = (id, value) => {
        const oldEl = document.getElementById(this.state.driveFilter + "Tick");
        oldEl.style.display = 'none';
        const newEl = document.getElementById(id + "Tick");
        newEl.style.display = 'block';

        this.setState({
            driveFilter: id,
            driverText: value
        });

        this.props.handle(value, this.state.engineText, this.state.searchByName);
    }

    engineHandle = (id, value) => {
        const oldEl = document.getElementById(this.state.engineFilter + "Tick");
        oldEl.style.display = 'none';
        const newEl = document.getElementById(id + "Tick");
        newEl.style.display = 'block';

        this.setState({
            engineFilter: id,
            engineText: value
        });

        this.props.handle(this.state.driverText, value, this.state.searchByName);
    }

    searchByNameHandle = (e, id) => {
        const input = document.getElementById(id);
        this.setState({
            searchByName: input.value
        });

        this.props.handle(this.state.driverText, this.state.engineText, input.value);
    }
 
    render() {
        const driveCheckboxes = [
            {
                id: 'driveAll',
                text: "Всі",
            },
            {
                id: 'driveFour',
                text: "Повний",
            },
            {
                id: 'driveBack',
                text: "Задній",
            },
            {
                id: 'driveFront',
                text: "Передній",
            }
        ];

        const engineCheckboxes = [
            {
                id: 'engineAll',
                text: "Всі",
            },
            {
                id: 'engineDiz',
                text: "Дизель",
            },
            {
                id: 'engineBenz',
                text: "Бензин",
            }
        ];

        return (
            <div className="filters">
                <Input id="searchByName" handle={this.searchByNameHandle} class="filters-search" type="standart" search="true" placeholder="Пошук авто..." />
                <Input class="filters-mark" type="list" search="true" placeholder="Марка авто" />
                <Input class="filters-model" type="list" placeholder="Модель" />
                
                <div className="drive">
                    <h2>Привід</h2>
                    <div className="checkboxes">
                       {
                            driveCheckboxes.map(ch => (
                                <Checkbox handle={this.driveHandle} checked={ this.state.driveFilter == ch.id ? true : false } id={ch.id} text={ch.text} />
                            ))
                       }
                    </div>
                </div>

                <div className="engine">
                    <h2>Двигун</h2>
                    <div className="checkboxes">
                        {
                            engineCheckboxes.map(ch => (
                                <Checkbox handle={this.engineHandle} checked={ this.state.engineFilter == ch.id ? true : false } id={ch.id} text={ch.text} />
                            ))
                       }
                    </div>
                </div>
            </div>
        );
    }   
}

export default Filters;