import React from 'react';

class Input extends React.Component {
    render() {
        let styles = new Object();

        if (this.props.fill == "standart" || !this.props.fill) {
            // styles["background"] = "#fff";
            styles["border"] = "0";
        } 

        if (this.props.type == "standart") {
            if (this.props.search) {
                return (
                    <div className={this.props.class + " input input-search"}>
                        <img className="input-search-glass" src="/img/glass-icon-dark.svg" alt="glass icon"/>
                        <input id={this.props.id} type="text" placeholder={this.props.placeholder} style={styles} />
                        <div className="input-search-button" onClick={(e, id) => {this.props.handle(e, this.props.id)}}>
                            <img src="/img/glass-icon-light.svg" alt="glass icon"/>
                        </div>
                    </div>
                );
            } else {
                return (
                    <div className={this.props.class + " input input-search"}>
                        <input id={this.props.id} type="text" placeholder={this.props.placeholder} style={styles} />
                    </div>
                );
            }
        } else if (this.props.type == "list") {
            if (this.props.search) {
                return (
                    <div className={this.props.class + " input input-search"}>
                        <img className="input-search-glass" src="/img/glass-icon-dark.svg" alt="glass icon"/>
                        <input type="text" placeholder={this.props.placeholder} style={styles} />
                        <img className="input-arrow-down" src="/img/arrow-down.svg" alt="down arrow"/>
                    </div>
                );
            } else {
                return (
                    <div className={this.props.class + " input"}>
                        <input type="text" placeholder={this.props.placeholder} style={styles} />
                        <img className="input-arrow-down" src="/img/arrow-down.svg" alt="down arrow"/>
                    </div>
                );
            }
        } else {
            return (
                <div className="input"></div>
            );
        }
    }
}

export default Input;
