import React from 'react';

import Property from './Property';

class Properties extends React.Component {
    render() {
        return (
            <div className="cc-properties">
                <div className="cc-props-col">
                    <Property header="Двигун" text={this.props.params.engine} />
                    <Property header="Розхід" text={this.props.params.fuelCons} />
                </div>

                <div className="cc-props-col">
                    <Property header="0-100 км/год" text={this.props.params.acceleration / 100 + " сек."} />
                    <Property header="Привід" text={this.props.params.wheelDrive} />
                </div>

                <div className="cc-props-col">
                    <Property header="КПП" text={this.props.params.gearbox} />
                    <Property header="Висота" text={this.props.params.height} />
                </div>
                
                <div className="cc-props-col">
                    <Property header="Ширина" text={this.props.params.width} />
                    <Property header="Довжина" text={this.props.params.length} />
                </div>
            </div>
        );
    }
}

export default Properties;