import React from 'react';

import Details from '../../../components/Details';
import { connect } from 'react-redux'; 
import { withRouter }   from 'react-router';
import CarsCatalogActions from '../../CarsCatalog/actions';

class DetailsContainer extends React.Component {
    componentDidMount() {
        const { post, postId, fetchItems } = this.props;
        if (!post) {
            fetchItems(postId);
        }
    }

    render() {
        return !this.props ? 'Loading.. ' : <Details {...this.props} />
    }
}

const mapStateToProps = ({ cars }, { match }) => ({
    car: cars.items && cars.items.filter(post => post._id === match.params.id)[0],
    carId: match.params.id
});

export default withRouter(
    connect(mapStateToProps, CarsCatalogActions)(DetailsContainer),
);